import React, { useState, useEffect } from "react";
import Heading from "../layouts/Heading";
import SkillCard from "../layouts/SkillsCard";
import db from "../index";
import { collection, getDocs } from "firebase/firestore";

function Skills() {
  const [skills, setSkills] = useState([]);

  const fetchProjects = async () => {
    try {
      const skillsDoc = await getDocs(collection(db, "skills"));
      const skillsList = skillsDoc.docs.map((doc) => ({
        ...doc.data(),
        id: doc.id
      }));
      setSkills(
        skillsList.filter(function (skill) {
          return skill.visible;
        })
      );
    } catch (err) {}
  };

  useEffect(() => {
    fetchProjects();
  }, []);

  return (
    <div id="skills">
      <Heading title="Skills and Abilities" />
      <div className="skills-conatiner">
        {skills.map((skill) => {
          const { id, title } = skill;
          return <SkillCard key={id} title={title} />;
        })}
      </div>
    </div>
  );
}

export default Skills;
