import React, { useState } from "react";
import Heading from "../layouts/Heading";
import ThankYouDialog from "../layouts/ThankYouDialog";
import GetInTouchInput from "../layouts/GetInTouchInput";
import Button from "@mui/material/Button";
import db from "../index";
import { collection, addDoc, Timestamp } from "firebase/firestore";

function GetInTouch() {
  const [open, setOpen] = useState(false);
  const [disableButton, setDisableButton] = useState(false);

  const [name, setName] = useState({
    text: "",
    error: false,
    errorMessage: ""
  });
  const [phone, setPhone] = useState({
    text: "",
    error: false,
    errorMessage: ""
  });
  const [email, setEmail] = useState({
    text: "",
    error: false,
    errorMessage: ""
  });
  const [message, setMessage] = useState({
    text: "",
    error: false,
    errorMessage: ""
  });

  async function submit() {
    // Name
    var nameFlag = true;
    if (name.text.length === 0) {
      setName((prevItems) => {
        return {
          ...prevItems,
          error: true,
          errorMessage: "Name cannot be empty"
        };
      });
      nameFlag = false;
    } else {
      setName((prevItems) => {
        return {
          ...prevItems,
          error: false,
          errorMessage: ""
        };
      });
      nameFlag = true;
    }

    // Phone
    var phoneFlag = true;
    const phoneRegExp = /[a-zA-Z]/g;
    if (phone.text.length === 0) {
      setPhone((prevItems) => {
        return {
          ...prevItems,
          error: true,
          errorMessage: "Phone number cannot be empty"
        };
      });
      phoneFlag = false;
    } else if (phoneRegExp.test(phone.text)) {
      setPhone((prevItems) => {
        return {
          ...prevItems,
          error: true,
          errorMessage: "Phone number cannot contain alphabet"
        };
      });
      phoneFlag = false;
    } else if (phone.text.length !== 10) {
      setPhone((prevItems) => {
        return {
          ...prevItems,
          error: true,
          errorMessage: "Invalid phone number"
        };
      });
      phoneFlag = false;
    } else {
      setPhone((prevItems) => {
        return {
          ...prevItems,
          error: false,
          errorMessage: ""
        };
      });
      phoneFlag = true;
    }

    // Email
    var emailFlag = true;
    const mailRegExp = /^[a-zA-Z0-9.a-zA-Z0-9.!#$%&'*+-/=?^_`{|}~]+@[a-zA-Z0-9]+\.[a-zA-Z]+/;
    if (email.text.length === 0) {
      setEmail((prevItems) => {
        return {
          ...prevItems,
          error: true,
          errorMessage: "Email cannot be empty"
        };
      });
      emailFlag = false;
    } else if (!email.text.match(mailRegExp)) {
      setEmail((prevItems) => {
        return {
          ...prevItems,
          error: true,
          errorMessage: "Please enter a valid email address"
        };
      });
      emailFlag = false;
    } else {
      setEmail((prevItems) => {
        return {
          ...prevItems,
          error: false,
          errorMessage: ""
        };
      });
      emailFlag = true;
    }

    // Message
    var messageFlag = true;
    if (message.text.length === 0) {
      setMessage((prevItems) => {
        return {
          ...prevItems,
          error: true,
          errorMessage: "Please write something"
        };
      });
      messageFlag = false;
    } else {
      setMessage((prevItems) => {
        return {
          ...prevItems,
          error: false,
          errorMessage: ""
        };
      });
      messageFlag = true;
    }

    if (!disableButton && nameFlag && phoneFlag && emailFlag && messageFlag) {
      try {
        await addDoc(collection(db, "getInTouch"), {
          name: name.text,
          phoneNumber: "+91 " + phone.text,
          email: email.text,
          message: message.text,
          createdAt: Timestamp.now(),
          platform: navigator.platform,
          vendor: navigator.vendor,
          userAgent: navigator.userAgent
        });
        setDisableButton(true);
        setOpen(true);
      } catch (e) {
        console.log("Error adding document: ", e);
      }
    }
  }

  return (
    <div id="getInTouch">
      <Heading title="Get In touch" color="#ffd60a" />
      <div className="getInTouch-container">
        <form autoComplete="off">
          <GetInTouchInput
            id="name"
            value={name.text}
            error={name.error}
            placeholder="Name"
            disableButton={disableButton}
            errorMessage={name.errorMessage}
            onChange={(event) =>
              setName((prevItems) => {
                return { ...prevItems, text: event.target.value };
              })
            }
          />
          <GetInTouchInput
            id="phone"
            value={phone.text}
            error={phone.error}
            placeholder="Phone Number"
            disableButton={disableButton}
            errorMessage={phone.errorMessage}
            onChange={(event) =>
              setPhone((prevItems) => {
                return { ...prevItems, text: event.target.value };
              })
            }
            type="tel"
            inputProps={{ maxLength: 10, minLength: 10 }}
          />
          <GetInTouchInput
            id="email"
            value={email.text}
            error={email.error}
            placeholder="Email"
            disableButton={disableButton}
            errorMessage={email.errorMessage}
            onChange={(event) =>
              setEmail((prevItems) => {
                return { ...prevItems, text: event.target.value };
              })
            }
            type="email"
          />
          <GetInTouchInput
            id="message"
            value={message.text}
            error={message.error}
            placeholder="Drop your message here"
            disableButton={disableButton}
            errorMessage={message.errorMessage}
            onChange={(event) =>
              setMessage((prevItems) => {
                return { ...prevItems, text: event.target.value };
              })
            }
            multiline={true}
            maxRows={5}
          />
          {disableButton ? (
            <></>
          ) : (
            <Button
              variant="contained"
              disableElevation
              style={{
                backgroundColor: "rgb(0, 8, 20)",
                borderRadius: 8,
                display: "flex",
                margin: "20px 0px 10px auto",
                padding: "10px 30px"
              }}
              onClick={submit}
            >
              Submit
            </Button>
          )}
        </form>
      </div>
      <ThankYouDialog
        open={open}
        handleClose={() => {
          setOpen(false);
        }}
      />
    </div>
  );
}

export default GetInTouch;
