import React, { useState } from "react";
import { useMediaQuery } from "react-responsive";
import "bootstrap/dist/css/bootstrap.min.css";
import { Container, Nav, Navbar } from "react-bootstrap";

function Header() {
  const mobile = useMediaQuery({
    query: "(max-width: 732px)"
  });

  const [expanded, setExpanded] = useState(false);

  function onClick(element) {
    setExpanded(false);
    setTimeout(
      function () {
        const temp = document.getElementById(element);
        let position = temp.getBoundingClientRect();
        window.scrollTo(position.left, position.top + window.scrollY - 60);
      },
      mobile ? 300 : 0
    );
  }

  return (
    <Navbar
      bg="dark"
      variant="dark"
      expand="lg"
      sticky="top"
      expanded={expanded}
    >
      <Container fluid>
        <Navbar.Brand href="/">Pratham</Navbar.Brand>
        <Navbar.Toggle onClick={() => setExpanded(!expanded)} />
        <Navbar.Collapse>
          <Nav className="justify-content-end flex-grow-1 pe-3">
            <Nav.Link
              onClick={() => {
                onClick("root");
              }}
            >
              Home
            </Nav.Link>
            <Nav.Link
              onClick={() => {
                onClick("about");
              }}
            >
              About
            </Nav.Link>
            <Nav.Link
              onClick={() => {
                onClick("experience");
              }}
            >
              Experience
            </Nav.Link>
            <Nav.Link
              onClick={() => {
                onClick("projects");
              }}
            >
              Projects
            </Nav.Link>
            <Nav.Link
              onClick={() => {
                onClick("skills");
              }}
            >
              Skills
            </Nav.Link>
            <Nav.Link
              onClick={() => {
                onClick("getInTouch");
              }}
            >
              GetInTouch
            </Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}

export default Header;
