import React, { useState, useEffect } from "react";
import Heading from "../layouts/Heading";
import ProjectCard from "../layouts/ProjectCard";
import Grid from "@mui/material/Unstable_Grid2";
import Button from "@mui/material/Button";
import { useMediaQuery } from "react-responsive";
import db from "../index";
import { collection, getDocs } from "firebase/firestore";

function Projects() {
  const mobile = useMediaQuery({
    query: "(max-width: 732px)"
  });

  const [projects, setProjects] = useState([]);
  const [length, setLength] = useState(mobile ? 3 : 6);

  async function fetchProjects() {
    try {
      const projectsDoc = await getDocs(collection(db, "projects"));
      const projectList = projectsDoc.docs.map((doc) => ({
        ...doc.data(),
        id: doc.id
      }));
      setProjects(
        projectList.filter(function (project) {
          return project.visible;
        })
      );
    } catch (err) {}
  }

  useEffect(() => {
    fetchProjects();
  }, []);

  return (
    <div id="projects">
      <Heading title="Projects" color="white" />
      <Grid
        container
        spacing={{ xs: 3, md: 3 }}
        columns={{ xs: 1, sm: 2, md: 3 }}
      >
        {projects.map(
          (project, index) =>
            index < length && (
              <Grid xs={1} sm={1} md={1} key={index}>
                <ProjectCard
                  key={project.id}
                  title={project.title}
                  desc={project.desc}
                  link={project.link}
                />
              </Grid>
            )
        )}
        {projects.length > length && (
          <Button
            variant="text"
            style={{
              marginTop: "20px",
              color: "#ffd60a",
              margin: "auto",
              backgroundColor: "transparent",
              textTransform: "none",
              textDecoration: "underline",
              fontSize: 16
            }}
            onClick={() => {
              setLength(projects.length);
            }}
          >
            Load More
          </Button>
        )}
      </Grid>
    </div>
  );
}

export default Projects;
