import { OutlinedInput, FormHelperText } from "@mui/material";

function GetInTouchInput(props) {
  return (
    <>
      <OutlinedInput
        id={props.id}
        value={props.value}
        type={props.type}
        fullWidth={true}
        required
        error={props.error}
        placeholder={props.placeholder}
        disabled={props.disableButton}
        inputProps={props.inputProps}
        onChange={props.onChange}
        multiline={props.multiline}
        maxRows={props.maxRows}
      />
      {props.error ? (
        <FormHelperText
          error
          id={props.id + "-error"}
          style={{
            display: "flex",
            justifyContent: "end",
            margin: "0 0 15px 0"
          }}
        >
          {props.errorMessage}
        </FormHelperText>
      ) : (
        <div
          style={{
            margin: "0 0 15px 0"
          }}
        ></div>
      )}
    </>
  );
}

GetInTouchInput.defaultProps = {
  type: "text",
  inputProps: {},
  multiline: false,
  maxRows: null
};

export default GetInTouchInput;
