import React, { useState, useEffect } from "react";
import Heading from "../layouts/Heading";
import ExperienceCard from "../layouts/ExperienceCard";
import { useMediaQuery } from "react-responsive";
import Button from "@mui/material/Button";
import db from "../index";
import { collection, getDocs, query, orderBy } from "firebase/firestore";

function Experience() {
  const mobile = useMediaQuery({
    query: "(max-width: 732px)"
  });
  const tablet = useMediaQuery({
    query: "(min-width: 768px)"
  });
  const desktop = useMediaQuery({
    query: "(min-width: 1366px)"
  });

  const [height, setHeight] = useState(0);
  const [length, setLength] = useState(3);
  const [experiences, setExperience] = useState([]);

  async function fetchExperience() {
    if (experiences.length === 0) {
      try {
        const experienceDoc = await getDocs(
          query(collection(db, "experiences"), orderBy("startDate", "desc"))
        );
        const experienceList = experienceDoc.docs.map((doc) => ({
          ...doc.data(),
          id: doc.id
        }));
        setExperience(
          experienceList.filter(function (experience) {
            return experience.visible;
          })
        );
      } catch (err) {}
    }
    setHeight(
      document.getElementById("experience").clientHeight -
        (mobile ? 210 : desktop ? 280 : 240) +
        (length === experiences.length && tablet && 20)
    );
  }

  useEffect(() => {
    fetchExperience();
  });

  return (
    <div id="experience">
      <Heading title="Experience" />
      <div
        className="experience-line"
        style={{
          height: height
        }}
      ></div>
      {experiences.map(
        (experience, index) =>
          index < length && (
            <ExperienceCard
              index={index}
              key={experience.id}
              title={experience.title}
              desc={experience.desc}
              timeperiod={experience.timeperiod}
            />
          )
      )}
      {experiences.length > length && (
        <Button
          variant="text"
          style={{
            marginTop: "20px",
            textTransform: "none",
            textDecoration: "underline",
            fontSize: 16
          }}
          onClick={() => {
            setLength(experiences.length);
          }}
        >
          Load More
        </Button>
      )}
    </div>
  );
}

export default Experience;
